		onShow: function() {
			var _this = this
            let templateType = getByKey(TOKEN_MESSAGE_CONTANTS.PATROL_TEMPLATE_TYPE)
            // 1、判断缓存中有没有templateType数据，没有就请求，如果请求不到跳转错误页面，
            // 2、进行缓存，如果无法链接网络，那么跳转至作错误页面
            if(templateType==null || templateType==''){
                var patrol = store.getters.patrolTemplateType
                if(patrol==undefined || patrol.length==0){
                    _this.$store.dispatch('GetPatrolTemplateType').then(res=>{

                    }).catch(error=>{
                        uni.navigateTo({
                            url: 'error?id=1&errMsg=无法连接网络,且没有缓存数据，请链接网络后重试'
                        });
                    })
                }else{
                    console.debug(store.getters.patrolTemplateType)
                }
            // 缓存中有分类信息，但vuex中没有
            }else if(store.getters.patrolTemplateType.length==0 ){
                _this.$store.dispatch('setPatrolTemplateTypeFromCache',templateType)
            }

            // 2、检查缓存中是否有模板信息内容
            let patrolTemplate = getByKey(TOKEN_MESSAGE_CONTANTS.PATROL_TEMPLATE)
            if(patrolTemplate.length==0){
                _this.$store.dispatch('GetPatrolTemplate').then(res=>{
                }).catch(error=>{
                    uni.navigateTo({
                        url: 'error?id=1&errMsg=无法加载巡检模板数据，请链接网络后重试'
                    });
                })
                // 向vuex中设置内容
            }else if(store.getters.patrolTemplate.length==0){
                _this.$store.dispatch('setPatrolTemplateFromCache',patrolTemplate)
            }
            // 判断用户是否登录了
            let token = getByKey(TOKEN_MESSAGE_CONTANTS.ACCESS_TOKEN_NAME);
            if(token==null || token===''){
                uni.redirectTo({
                    url: './pages/electric/index'
                });
            }else{
                uni.redirectTo({
                    url: './pages/electric/inspectionIndex'
                });
            }
						//判断审核人信息
						let checkUser = getByKey('submitUser');
						if(checkUser==null || checkUser===''){
							_this.$store.dispatch('getSubmitUserList')
						}
		},
